.weatherContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #E3F2FD 0%, #B3E5FC 100%);
  min-height: 100vh;
}

.header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  color: #1976D2;
}

.header h1 {
  margin: 0;
  color: #0D47A1;
  font-size: 28px;
}

.header p {
  margin: 8px 0 0 0;
  color: #666;
}

.regionSelector {
  margin-bottom: 30px;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.regionSelector h2 {
  margin: 0 0 15px 0;
  color: #0D47A1;
  font-size: 18px;
}

.regionGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 10px;
}

.regionButton {
  padding: 10px 16px;
  border: 2px solid #1976D2;
  background: white;
  color: #1976D2;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  transition: all 0.3s ease;
}

.regionButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(25, 118, 210, 0.2);
}

.regionButton.active {
  background: linear-gradient(135deg, #1976D2 0%, #0D47A1 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(25, 118, 210, 0.4);
}

.content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.weatherCard {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  grid-column: 1 / -1;
}

.weatherCard h2 {
  margin: 0;
  color: #0D47A1;
  font-size: 24px;
  margin-bottom: 15px;
}

.weatherMain {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 20px;
}

.weatherIcon {
  font-size: 60px;
  min-width: 60px;
}

.weatherInfo {
  flex: 1;
}

.weatherInfo h2 {
  margin: 0;
  color: #0D47A1;
  font-size: 24px;
}

.condition {
  font-size: 16px;
  color: #666;
  margin: 5px 0;
}

.tempDisplay {
  display: flex;
  align-items: baseline;
  gap: 10px;
  margin-top: 10px;
}

.temp {
  font-size: 32px;
  font-weight: bold;
  color: #1976D2;
}

.currentWeatherCard {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.currentWeatherCard h2 {
  margin: 0 0 20px 0;
  color: #0D47A1;
}

.weatherMain {
  text-align: center;
  margin-bottom: 20px;
}

.weatherIcon {
  font-size: 60px;
  margin-bottom: 10px;
}

.temperature {
  font-size: 36px;
  font-weight: bold;
  color: #1976D2;
  margin: 10px 0;
}

.condition {
  font-size: 16px;
  color: #666;
  margin-bottom: 5px;
}

.advice {
  background: #E3F2FD;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #1976D2;
  margin-top: 15px;
}

.advice p {
  margin: 0;
  color: #0D47A1;
  font-size: 14px;
  line-height: 1.6;
}

.metricsGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

.metric {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #1976D2;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.metric .label {
  display: block;
  color: #666;
  font-size: 12px;
  margin-bottom: 5px;
}

.metric .value {
  display: block;
  color: #1976D2;
  font-size: 20px;
  font-weight: bold;
}

.metricItem {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #1976D2;
}

.metricLabel {
  display: block;
  color: #666;
  font-size: 12px;
  margin-bottom: 5px;
}

.metricValue {
  display: block;
  color: #1976D2;
  font-size: 20px;
  font-weight: bold;
}

.alertsSection {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.alertsSection h2 {
  margin: 0 0 20px 0;
  color: #0D47A1;
}

.alertsList {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 15px;
}

.alertCard {
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid;
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.alertCard.good {
  background: #E8F5E9;
  border-left-color: #2E7D32;
}

.alertCard.warning {
  background: #FFF3E0;
  border-left-color: #F57C00;
}

.alertIcon {
  font-size: 20px;
  flex-shrink: 0;
}

.alertContent {
  flex: 1;
}

.alertContent h4 {
  margin: 0 0 5px 0;
  font-size: 14px;
  font-weight: 600;
}

.alertCard.good .alertContent h4 {
  color: #1a472a;
}

.alertCard.warning .alertContent h4 {
  color: #E65100;
}

.alertContent p {
  margin: 0;
  font-size: 12px;
  color: #666;
}

.seasonalGuideSection {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.seasonCard {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.seasonCard h3 {
  margin: 0 0 15px 0;
  color: #0D47A1;
  font-size: 16px;
  border-bottom: 2px solid #1976D2;
  padding-bottom: 10px;
}

.seasonCard ul {
  margin: 0;
  padding-left: 20px;
  color: #666;
  font-size: 14px;
}

.seasonCard li {
  margin-bottom: 8px;
  line-height: 1.5;
}

.seasonCard li strong {
  color: #0D47A1;
}

@media (max-width: 1024px) {
  .content {
    grid-template-columns: 1fr;
  }

  .regionSelector {
    justify-content: center;
  }
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .regionButton {
    padding: 8px 12px;
    font-size: 12px;
  }

  .metricsGrid {
    grid-template-columns: 1fr;
  }

  .seasonalGuideSection {
    grid-template-columns: 1fr;
  }

  .temperature {
    font-size: 28px;
  }

  .weatherIcon {
    font-size: 48px;
  }
}
