.soilTestingContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  min-height: 100vh;
}

.header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  color: #2E7D32;
}

.header h1 {
  margin: 0;
  color: #1a472a;
  font-size: 28px;
}

.header p {
  margin: 8px 0 0 0;
  color: #666;
}

.content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.formSection {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.formSection h2 {
  color: #1a472a;
  margin-top: 0;
  margin-bottom: 20px;
}

.formGroup {
  margin-bottom: 20px;
}

.formGroup label {
  display: block;
  margin-bottom: 8px;
  color: #333;
  font-weight: 600;
  font-size: 14px;
}

.formGroup input[type="text"],
.formGroup select {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.3s ease;
}

.formGroup input[type="text"]:focus,
.formGroup select:focus {
  outline: none;
  border-color: #2E7D32;
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.1);
}

.twoColumn {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 15px;
}

.sliderGroup {
  display: flex;
  align-items: center;
  gap: 10px;
}

.sliderGroup input[type="range"] {
  flex: 1;
  height: 6px;
  border-radius: 3px;
  background: #ddd;
  outline: none;
  -webkit-appearance: none;
}

.sliderGroup input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #2E7D32;
  cursor: pointer;
  transition: background 0.3s ease;
}

.sliderGroup input[type="range"]::-webkit-slider-thumb:hover {
  background: #1a472a;
}

.sliderGroup input[type="range"]::-moz-range-thumb {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: #2E7D32;
  cursor: pointer;
  border: none;
}

.value {
  min-width: 50px;
  text-align: right;
  font-weight: 600;
  color: #2E7D32;
}

.submitButton {
  width: 100%;
  padding: 14px;
  background: linear-gradient(135deg, #2E7D32 0%, #1a472a 100%);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.submitButton:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(46, 125, 50, 0.3);
}

.submitButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.resultSection {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.resultSection h2 {
  color: #1a472a;
  margin-top: 0;
  margin-bottom: 20px;
}

.ratingCard {
  text-align: center;
  padding: 20px;
  background: linear-gradient(135deg, #E8F5E9 0%, #F1F8E9 100%);
  border-radius: 12px;
  margin-bottom: 20px;
}

.ratingCircle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background: linear-gradient(135deg, #2E7D32 0%, #1a472a 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 15px;
  box-shadow: 0 4px 12px rgba(46, 125, 50, 0.2);
}

.ratingValue {
  color: white;
  font-size: 32px;
  font-weight: bold;
}

.ratingCard p {
  margin: 0;
  color: #1a472a;
  font-weight: 600;
}

.metricsGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

.metricItem {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #2E7D32;
}

.metricItem span {
  display: block;
  color: #666;
  font-size: 12px;
  margin-bottom: 5px;
}

.metricItem strong {
  display: block;
  color: #2E7D32;
  font-size: 20px;
  font-weight: bold;
}

.adviceBox {
  background: #E8F5E9;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #2E7D32;
  margin-bottom: 15px;
}

.adviceBox h3 {
  margin: 0 0 10px 0;
  color: #1a472a;
  font-size: 14px;
}

.adviceBox p {
  margin: 0;
  color: #1a472a;
  font-size: 14px;
  line-height: 1.6;
}

.cropsBox {
  background: #F1F8E9;
  padding: 15px;
  border-radius: 8px;
}

.cropsBox h3 {
  margin: 0 0 10px 0;
  color: #1a472a;
  font-size: 14px;
}

.cropsList {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.cropTag {
  background: #2E7D32;
  color: white;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
}

@media (max-width: 1024px) {
  .content {
    grid-template-columns: 1fr;
  }

  .twoColumn {
    grid-template-columns: 1fr;
  }

  .metricsGrid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .metricsGrid {
    grid-template-columns: 1fr;
  }
}
