.marketContainer {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, hsl(37, 100%, 94%) 0%, #FFE0B2 100%);
  min-height: 100vh;
}

.header {
  display: flex;
  align-items: center;
  gap: 20px;
  margin-bottom: 30px;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  color: #F57C00;
}

.header h1 {
  margin: 0;
  color: #E65100;
  font-size: 28px;
}

.header p {
  margin: 8px 0 0 0;
  color: #666;
}

.cropSelector {
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.cropSelector h2 {
  margin: 0 0 15px 0;
  color: #E65100;
  font-size: 18px;
}

.cropGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  gap: 12px;
}

.cropButton {
  padding: 12px;
  border: 2px solid #F57C00;
  background: white;
  color: #F57C00;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 13px;
  transition: all 0.3s ease;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.cropButton span {
  font-size: 24px;
}

.cropButton:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(245, 124, 0, 0.2);
}

.cropButton.active {
  background: linear-gradient(135deg, #F57C00 0%, #E65100 100%);
  color: white;
  box-shadow: 0 4px 12px rgba(245, 124, 0, 0.4);
}

.priceCard {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.priceHeader {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.cropInfo {
  flex: 1;
}

.cropInfo h2 {
  margin: 0;
  color: #E65100;
  font-size: 20px;
}

.priceDisplay {
  background: linear-gradient(135deg, #FFF3E0 0%, #FFE0B2 100%);
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  text-align: center;
}

.price {
  display: block;
  font-size: 36px;
  font-weight: bold;
  color: #E65100;
  margin-bottom: 10px;
}

.unit {
  display: block;
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}

.trendBadge {
  display: inline-flex;
  align-items: center;
  gap: 5px;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  margin-top: 10px;
}

.trendBadge.up {
  background: #E8F5E9;
  color: #1a472a;
}

.trendBadge.down {
  background: #FFEBEE;
  color: #C62828;
}

.trendBadge.stable {
  background: #E3F2FD;
  color: #0D47A1;
}

.metricsGrid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 15px;
  margin-bottom: 20px;
}

.metric {
  background: #f8f9fa;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #F57C00;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.metric span {
  color: #666;
  font-size: 12px;
  margin-bottom: 5px;
}

.metric strong {
  color: #E65100;
  font-size: 16px;
  font-weight: bold;
}

.detailsBox {
  background: #FFF3E0;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #F57C00;
  margin-bottom: 15px;
}

.detailsBox h4 {
  margin: 0 0 8px 0;
  color: #E65100;
  font-size: 14px;
}

.detailsBox p {
  margin: 0;
  color: #666;
  font-size: 13px;
  line-height: 1.5;
}

.tipsBox {
  background: #FFF3E0;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #F57C00;
}

.tipsBox h4 {
  margin: 0 0 8px 0;
  color: #E65100;
  font-size: 14px;
}

.tipsBox p {
  margin: 0;
  color: #666;
  font-size: 13px;
  line-height: 1.5;
}

.pricesTable {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  margin-bottom: 30px;
}

.pricesTable h2 {
  margin: 0 0 20px 0;
  color: #E65100;
}

.tableScroll {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
}

.table thead {
  background: #FFF3E0;
}

.table th {
  padding: 12px;
  text-align: left;
  font-weight: 600;
  color: #E65100;
  border-bottom: 2px solid #F57C00;
}

.table td {
  padding: 12px;
  border-bottom: 1px solid #eee;
  color: #666;
  font-size: 14px;
}

.table tr:hover {
  background: #FFF8F0;
}

.tipsSection {
  background: white;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.tipsSection h2 {
  margin: 0 0 20px 0;
  color: #E65100;
}

.tipsGrid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 15px;
}

.tipCard {
  background: #FFF3E0;
  padding: 15px;
  border-radius: 8px;
  border-left: 4px solid #F57C00;
}

.tipCard h4 {
  margin: 0 0 8px 0;
  color: #E65100;
  font-size: 14px;
}

.tipCard p {
  margin: 0;
  color: #666;
  font-size: 12px;
  line-height: 1.5;
}

@media (max-width: 1024px) {
  .content {
    grid-template-columns: 1fr;
  }

  .cropButtons {
    grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  }

  .tipsList {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    text-align: center;
  }

  .cropButtons {
    grid-template-columns: repeat(3, 1fr);
  }

  .price {
    font-size: 28px;
  }

  .detailsGrid {
    grid-template-columns: 1fr;
  }

  .tipsList {
    grid-template-columns: repeat(2, 1fr);
  }

  .outlookSection {
    grid-template-columns: 1fr;
  }

  .pricesTableSection {
    font-size: 12px;
  }

  .pricesTable th,
  .pricesTable td {
    padding: 8px;
  }
}
