.signupContainer {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #1a472a 0%, #2e7d32 50%, #43a047 100%);
  overflow: hidden;
  position: relative;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
}

.backgroundGradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(
    circle at 20% 50%,
    rgba(76, 175, 80, 0.2) 0%,
    transparent 50%
  ),
  radial-gradient(
    circle at 80% 80%,
    rgba(66, 165, 162, 0.2) 0%,
    transparent 50%
  );
  animation: gradientShift 15s ease infinite;
  z-index: 1;
}

@keyframes gradientShift {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.8;
  }
}

.floatingShape1,
.floatingShape2 {
  position: absolute;
  border-radius: 50%;
  opacity: 0.1;
  filter: blur(40px);
  animation: float 6s ease-in-out infinite;
  z-index: 2;
}

.floatingShape1 {
  width: 400px;
  height: 400px;
  background: rgba(255, 255, 255, 0.5);
  top: -100px;
  left: -50px;
  animation-duration: 8s;
}

.floatingShape2 {
  width: 300px;
  height: 300px;
  background: rgba(255, 255, 255, 0.3);
  bottom: -50px;
  right: 10%;
  animation-duration: 10s;
}

@keyframes float {
  0%, 100% {
    transform: translateY(0px) translateX(0px);
  }
  50% {
    transform: translateY(-30px) translateX(20px);
  }
}

.signupCard {
  position: relative;
  z-index: 3;
  width: 100%;
  max-width: 480px;
  padding: 48px 32px;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  border-radius: 24px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3),
    0 0 1px rgba(255, 255, 255, 0.5) inset;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 20px;
  max-height: 90vh;
  overflow-y: auto;
}

/* Scrollbar styling */
.signupCard::-webkit-scrollbar {
  width: 6px;
}

.signupCard::-webkit-scrollbar-track {
  background: transparent;
}

.signupCard::-webkit-scrollbar-thumb {
  background: #ddd;
  border-radius: 3px;
}

.signupCard::-webkit-scrollbar-thumb:hover {
  background: #bbb;
}

/* Progress Indicator */
.progressContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 32px;
  gap: 0;
}

.progressStep {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  flex: 0 0 auto;
}

.stepNumber {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #f0f0f0;
  color: #999;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 16px;
  transition: all 0.3s ease;
}

.stepNumber.active {
  background: linear-gradient(135deg, #2E7D32 0%, #1a472a 100%);
  color: #fff;
  box-shadow: 0 4px 12px rgba(46, 125, 50, 0.3);
}

.stepNumber.completed {
  background: #E8F5E9;
  color: #2E7D32;
}

.stepLabel {
  font-size: 12px;
  font-weight: 600;
  color: #999;
  margin: 0;
  text-transform: uppercase;
  white-space: nowrap;
}

.progressLine {
  flex: 1;
  height: 2px;
  background: #f0f0f0;
  margin: 0 8px 20px 8px;
  transition: all 0.3s ease;
}

.progressLine.completed {
  background: linear-gradient(90deg, #2E7D32 0%, #43a047 100%);
}

/* Header */
.header {
  text-align: center;
  margin-bottom: 24px;
}

.title {
  font-size: 28px;
  font-weight: 700;
  color: #1a472a;
  margin: 0;
  margin-bottom: 8px;
  letter-spacing: -0.5px;
}

.subtitle {
  font-size: 14px;
  color: #666;
  margin: 0;
  font-weight: 500;
}

/* Form */
.form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.label {
  font-size: 12px;
  font-weight: 600;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.3px;
}

.inputWrapper,
.phoneWrapper,
.passwordWrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.inputIcon {
  position: absolute;
  left: 14px;
  font-size: 18px;
  color: #999;
  pointer-events: none;
}

.input {
  width: 100%;
  padding: 12px 14px 12px 40px;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 14px;
  background: #fff;
  color: #333;
  transition: all 0.3s ease;
  font-family: inherit;
}

.input:focus {
  outline: none;
  border-color: #2E7D32;
  background: #fafafa;
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.1);
}

.input:disabled {
  background: #f5f5f5;
  color: #999;
  cursor: not-allowed;
}

.input.error {
  border-color: #C62828;
  background: #fff;
}

.input.error:focus {
  box-shadow: 0 0 0 3px rgba(198, 40, 40, 0.1);
}

.countryCode {
  position: absolute;
  left: 40px;
  font-weight: 600;
  color: #666;
  font-size: 13px;
  pointer-events: none;
}

.phoneWrapper .input {
  padding-left: 70px;
}

.passwordWrapper .input {
  padding-right: 44px;
}

.passwordToggle {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  font-size: 18px;
  cursor: pointer;
  color: #666;
  padding: 8px;
  transition: all 0.2s ease;
}

.passwordToggle:hover {
  color: #2E7D32;
}

.errorMessage {
  font-size: 12px;
  color: #C62828;
  margin: 0;
  margin-top: 2px;
}

.helperText {
  font-size: 12px;
  color: #999;
  margin: 2px 0 0 0;
}

/* Terms Group */
.termsGroup {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-top: 8px;
}

.checkboxLabel {
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  font-size: 13px;
  color: #666;
  font-weight: 500;
}

.checkbox {
  width: 18px;
  height: 18px;
  cursor: pointer;
  accent-color: #2E7D32;
}

.checkbox:disabled {
  cursor: not-allowed;
  opacity: 0.6;
}

.link {
  color: #2E7D32;
  text-decoration: none;
  font-weight: 600;
  transition: all 0.2s ease;
}

.link:hover {
  color: #1a472a;
  text-decoration: underline;
}

/* Submit Button */
.submitButton {
  padding: 13px 24px;
  background: linear-gradient(135deg, #2E7D32 0%, #1a472a 100%);
  color: #fff;
  border: none;
  border-radius: 10px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 12px rgba(46, 125, 50, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-top: 8px;
}

.submitButton:hover {
  box-shadow: 0 6px 20px rgba(46, 125, 50, 0.4);
  transform: translateY(-2px);
}

.submitButton:active {
  transform: translateY(0);
  box-shadow: 0 2px 8px rgba(46, 125, 50, 0.3);
}

.submitButton:disabled {
  opacity: 0.7;
  cursor: not-allowed;
  transform: none;
}

/* Login Link */
.loginLink {
  text-align: center;
  font-size: 13px;
  color: #666;
  margin-top: 16px;
  margin-bottom: 0;
}

.loginSpan {
  color: #2E7D32;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
}

.loginSpan:hover {
  color: #1a472a;
}

/* Verify Content */
.verifyContent {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 300px;
}

.verifyBox {
  width: 100%;
  text-align: center;
}

.verifyText {
  font-size: 14px;
  color: #666;
  margin-bottom: 24px;
}

.verifyText strong {
  color: #2E7D32;
  font-weight: 600;
}

.otpInputs {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 10px;
  margin-bottom: 24px;
}

.otpInput {
  width: 100%;
  aspect-ratio: 1;
  border: 2px solid #e0e0e0;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 700;
  text-align: center;
  background: #f9f9f9;
  color: #333;
  cursor: text;
  transition: all 0.3s ease;
}

.otpInput:focus {
  outline: none;
  border-color: #2E7D32;
  background: #fff;
  box-shadow: 0 0 0 3px rgba(46, 125, 50, 0.1);
}

.resendText {
  font-size: 13px;
  color: #999;
  margin-top: 16px;
  margin-bottom: 0;
}

.resendLink {
  color: #2E7D32;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.resendLink:hover {
  color: #1a472a;
  text-decoration: underline;
}

/* Success Content */
.successContent {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 400px;
}

.successBox {
  width: 100%;
  text-align: center;
}

.successIcon {
  font-size: 72px;
  color: #2E7D32;
  margin-bottom: 16px;
  display: block;
}

.successTitle {
  font-size: 24px;
  font-weight: 700;
  color: #1a472a;
  margin: 0 0 12px 0;
}

.successMessage {
  font-size: 14px;
  color: #666;
  margin: 0 0 24px 0;
  line-height: 1.6;
}

/* Responsive Design */
@media (max-width: 480px) {
  .signupCard {
    padding: 28px 20px;
    border-radius: 20px;
    margin: 16px;
  }

  .progressContainer {
    margin-bottom: 24px;
  }

  .progressStep {
    gap: 6px;
  }

  .stepNumber {
    width: 36px;
    height: 36px;
    font-size: 14px;
  }

  .stepLabel {
    font-size: 10px;
  }

  .progressLine {
    margin: 0 4px 16px 4px;
  }

  .title {
    font-size: 24px;
  }

  .subtitle {
    font-size: 13px;
  }

  .form {
    gap: 14px;
  }

  .formGroup {
    gap: 5px;
  }

  .input {
    font-size: 13px;
    padding: 11px 12px 11px 38px;
  }

  .otpInputs {
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
  }

  .otpInput {
    font-size: 16px;
  }
}

@media (max-width: 360px) {
  .signupCard {
    padding: 20px 16px;
  }

  .title {
    font-size: 20px;
  }

  .progressContainer {
    gap: 0;
  }

  .progressLine {
    margin: 0 2px 16px 2px;
  }

  .stepLabel {
    font-size: 9px;
  }
}
